import logo from "./../../assets/images/logo.png";
import { Form, Input, Button, InputValue, MessagePlugin } from "tdesign-react";
import {createRef, useEffect, useState } from 'react';
import { apiUserLogin } from '../../api/api'
import { setToken } from "../../utils/cookie";
import jwt_decode from 'jwt-decode'
const { FormItem } = Form;
export interface UserLoginData {
  userName: string;
  password: string;
}

function Login(props: { history: string[]; }) {
  // 
  const [userName] = useState('eagle')
  const [password] = useState('eagle')
  const form = createRef();

  // 
  function loginHandle(e:any){
    if (e.validateResult === true) {
      apiUserLogin({userName, password}).then((res:any) => {
        if(Number(res.code) === 0){
          const token = res.data.token ?? res.data
          const user = jwt_decode(token)
          setToken(token)
          props.history.push('/')
        } else {
          MessagePlugin.error(res.msg);
        }
      })
    }
  }
  
  return (
    <div className="Login">
      <div className="leftCont"></div>
      <div className="ritCont">
        <div className="loginCont">
          <img src={logo} width="179px" alt="" />
          <Form ref={form} onSubmit={loginHandle}>
            <FormItem name="error" initialData={userName} rules={[{ required: true, message: '', type: 'error' }]}>
              <Input
                placeholder=""
              />
            </FormItem>
            <FormItem name="warning" initialData={password} rules={[{ required: true, message: '', type: 'error' }]}>
              <Input
                type="password"
                placeholder=""
              />
            </FormItem>
            <FormItem>
              <Button shape="round" type="submit" variant="base">
                
              </Button>
            </FormItem>
          </Form>
        </div>
      </div>
    </div>
  );
}

export default Login;
